  .switch {
    margin-left: 12px;
    margin-right: 12px;
  }
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #4b48db;
  border-radius: 20px;
  transition: all .4s;
  display: block;
  position: absolute;
  inset: 0;
}

.slider.round:before {
  border-radius: 50%;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4b48db;
  transition: all .4s;
  position: absolute;
  top: 2px;
  left: 1px;
}

.switch input:checked + .slider {
  background-color: #4b48db;
  border: 1px solid #fff;
}

.switch input:checked + .slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

.switch input:focus + .slider {
  box-shadow: 0 0 1px #4b48db;
}

